(function(){
    'use strict'
    angular
        .module('uclguide')
        .directive('directionIndicator',directionIndicator);

    /** @ngInject */
    function directionIndicator(responsiveService,deviceDetector){

        return {
            restrict: 'E',
            scope: {
                portrait: '=',
                landscape: '='
            },
            template:'<div ng-show="active" id="deviceDialog"><div class="msg" ng-include src="template"></div></div>',
            transclude: true,
            link: function (scope, element, attr) {

                scope.active = false;

                var checkOrientatation = function(n){
                    if(scope.portrait || !scope.landscape){
                        if(n === 'landscape'){
                            $('#app').addClass('hidden-for-small-up');
                            $(element).show();
                        }
                        else{
                            $('#app').removeClass('hidden-for-small-up');
                            $(element).hide();
                        }
                    }
                }

                if(deviceDetector.isMobile() && !deviceDetector.isTablet() && !deviceDetector.isDesktop()){

                    if(scope.portrait || !scope.landscape){
                        scope.template = 'app/components/orientation/landscape-overlay.html'
                    }

                    scope.active = true;
                    scope.$watch(function(){
                        return responsiveService('orientation')
                    }, function(n,o){
                        checkOrientatation(n)
                    })

                    $('body').on('resize', function(){
                        checkOrientatation(responsiveService('orientation'));
                    })

                    checkOrientatation(responsiveService('orientation'));

                }
                else{
                    scope.active = false;
                }


                scope.$on('$destroy', function(){
                })
            }
        }
    };

})();
(function() {
    'use strict';

    angular
        .module('uclguide')
        .directive('prevNext', prevNext);

    /** @ngInject */
    function prevNext($log, $sce,$state, VideoService) {
        return {
            restrict: 'E',
            template: '<div id="{{ name }}" class="{{ position }}"><button class="button trans icon" ng-click="onToggle()" name="{{name}}" ><i class="ucl-{{position}}"></i></button></div>',
            scope: {
                type: '@'
            },
            link: function ($scope, $element, $attri) {
                if($scope.type ==='next'){
                    $scope.name = 'next'
                    $scope.position = 'right'
                }
                else{
                    $scope.name = 'prev'
                    $scope.position = 'left'
                }

                $scope.onToggle = function(){

                    if($scope.type ==='next'){
                        VideoService.setNext();

                    }
                    else{
                        VideoService.setPrev();
                    }

                    $state.go('app.video.interface.item',{id:VideoService.getCurrent()});
                }

            }
        }
    }
})();
(function() {
  'use strict';

  angular
    .module('uclguide')
    .directive('acmeNavbar', acmeNavbar)
    .directive('logoToggle', logoToggle);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($scope,$log,$state,$previousState) {
      var vm = this;
      $scope.goBack = function(){
        $log.debug('GO BACK',$previousState.get(),$state.current.name);
        if($previousState.get()!== null){
          $previousState.go();
        }
        else{
          if($state.current.name !== 'app.video.interface.mobile'){
            $state.go('app.intro');
          }
          else{
            $state.go('app.video.interface.item');
          }
        }
      }
    }


  }
  /** @ngInject */
  function logoToggle($log,$rootScope,$state,$timeout){
    return {
      restrict: 'C',
      controller: function(){

      },
      link: function($scope,$element,$attr){

        $scope.white = true;
        $scope.black = false;

        $timeout(function(){
          checkButtons($state.current.name)
        },500);


        var checkButtons = function(name){
          $log.debug('$state',$state.current.name)
            switch(name){
              case 'app.video.info' :
                $scope.showprocedure= false;
                $scope.showclose= true;
                break;
              case 'app.info' :
                $scope.showprocedure= false;
                $scope.showclose= true;
                break;
              case 'app.video.interface.mobile' :
                $scope.showprocedure= false;
                $scope.showclose= true;
                break;
              default :
                $scope.showprocedure= true;
                $scope.showclose= false;
                break;
            }
        }



        $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams, options){
          $scope.showprocedure= false;
          $scope.showclose= false;
        });

        $rootScope.$on('gsapifyRouter:enterSuccess', function(event){
            checkButtons($state.current.name);
        })

        $rootScope.$on('onLogoToggle', function(e,args){
          switch (args.color){
            case 'w' :
            {
              $scope.white = true;
              $scope.black = false;
              break;
            }
            case 'b':
            {
              $scope.white = false;
              $scope.black = true;
              break;
            }
            default : {
              $scope.white = true;
              $scope.black = false;
            }
          }

        });



      }
    }
  }


})();

(function() {
  'use strict';

  angular
    .module('uclguide')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
    'use strict';

    angular
        .module('uclguide')
        .factory('VideoService', VideoService);

    /** @ngInject */
    function VideoService($timeout,$log,$q,$http,contentUrl,$sce,$rootScope,deviceDetector) {

        var obj = {
            baseUrl:null,
            playerAPI:null,
            seekpos:0,
            videos: null,
            current: 0,
            cuePoints : [],
            total:null,
            currentDuration:0,
            dir: 1
        }

        obj.fetchContent = function(){
            var deferred = $q.defer();
            $log.debug(contentUrl);
            $http.get(contentUrl+'videos.json')
                .success(function(data) {
                    var v = angular.fromJson(data);
                    obj.baseUrl = v[0].videoBaseUrl;
                    obj.videos = v[0].videos;
                    obj.total = v[0].videos.length;
                    deferred.resolve(data);
                }).error(function(msg, code) {
                deferred.reject(msg);
                $log.error(msg, code);
            });
            return deferred.promise;
        }

        obj.getAllImages = function(){
            $log.debug('GET ALL IMAGES');
            var v =  obj.videos;
            var imageArray = [];


            angular.forEach(v, function(value, key) {
                $log.debug('v',value)
                if(!deviceDetector.isPhoneOnly()){
                    imageArray.push('assets/images/video/'+value.source.thumb);
                    imageArray.push('assets/images/video/'+value.source.image);
                }
                else{
                    imageArray.push('assets/images/video/'+value.source.mob);
                }
            })

            return imageArray;
        }

        obj.setPlayerAPI = function($api){
            obj.playerAPI = $api;
        }

        obj.getPlayerAPI = function(){
            if(obj.playerAPI!==null){
                return obj.playerAPI;
            }
        }

        obj.setCurrent = function($id){
            if($id <= (obj.total-1)){
                return obj.current = $id;
            }
            else{
                return null;
            }
        };

        obj.getCurrent = function(){
            return obj.current;
        }

        obj.setNext = function(){
            if(obj.current !== (obj.total-1)){
                obj.current ++
            }
            else{
                obj.current = 0;
            }
            obj.dir = 1;
        }

        obj.setPrev = function(){
            if(obj.current > 0){
                obj.current --;

            }
            else{
                obj.current = obj.total-1
            }
            obj.dir = 0;
        }

        obj.getVideoCurrent = function(){
            return obj.getVideoById(obj.current);
        }

        obj.getVideoById = function($id){
            if(obj.videos===null){
                return;
            }
            return obj.videos[$id]
        }

        obj.getVideoInfoById = function($id){
            var video =  obj.videos[$id];
            return video.info;
        }


        obj.getVideoSourceById = function($id){

            if(obj.videos===null){
                return;
            }
            var video =  obj.videos[$id];

            var videoData =  {
                source: [],
                poster:'',
            };

            var videoName = video.source.name;
            var type = video.source.type;
            var source = [];

            angular.forEach(type, function(value, key) {
                videoData.source.push({src:$sce.trustAsResourceUrl(obj.baseUrl+''+videoName+'.'+value),type:'video/'+value})
            });

            videoData.poster = video.source.image;

            $log.debug('video data',videoData)
            return videoData;
        }

        var calcPosition = function($id,$time,$duration){
            var t = obj.total;
            var range = 100/obj.total;
            var time = (range/100)*(parseInt((100/$duration)*$time));
            var pos = parseInt(((100/t)*$id)+time);

            return pos;
        }

        obj.getVideoInfoPoints = function(){

            if(obj.videos===null){
                return;
            }

            obj.cuePoints = [];
            var count=0;


            angular.forEach(obj.videos, function(value,key){

                var videoID = value.id;
                var duration = value.totalTime;

                angular.forEach(value.cue, function(value,key){
                    var temp = {
                        id:count++,
                        video:videoID,
                        content:value.content,
                        timeings: [value.start,value.end],
                        active:false,
                        pos: calcPosition(videoID,value.start,duration),
                        end: calcPosition(videoID,value.end,duration)
                    }

                    obj.cuePoints.push(temp);
                });
            })

            $log.debug('CUE POINTS',obj.cuePoints )

            return obj.cuePoints ;
        }

        obj.checkVideoInfoPoints = function($time){
            return $q(function(resolve,reject){
                angular.forEach(obj.cuePoints, function(value,key){
                    if(($time*10) >= value.pos && ($time*10) <= value.end){
                        if(!value.active){
                            resolve(value);
                        }
                    }
                    else{
                        if(value.active){
                            reject(value);
                        }
                    }
                })

            })
        }

        obj.seekbarUpdate = function($time,$duration){
            var time = 0
            obj.currentDuration = $duration;
            if(typeof $time !== 'undefined'){
                var range = 100/obj.total;
                time = (range/100)*(parseInt((100/$duration)*$time));
            }
            var c = obj.getCurrent();
            var t = obj.total;
            obj.seekpos = ((100/t)*c)+time;

            obj.checkVideoInfoPoints(obj.seekpos/obj.total).then(
            function(success){
                $log.debug('Video show',success.content.title);
                $rootScope.$broadcast('showTooltip',success);
            },
            function(reject){
                $log.debug('video hide',reject.content.title);
                $rootScope.$broadcast('hideTooltip',reject);
            });

            return obj.seekpos;
        }

        obj.getSeekbarPos = function(){
            return obj.seekpos;
        }

        obj.getMovieSeekPos = function($seek){
            var c = obj.getCurrent();
            var t = obj.total;
            var current = ((100/t)*c);

            var seek = ($seek-current)*10;
            $log.debug('SEEK',$seek,seek,current);
            return seek;
        }



        return obj;
    }
})();

(function() {
    'use strict';
    angular
        .module('ng.reponsive',[])
        .factory('responsiveService', responsiveService)
        .value('responsiveConfig', {
            sizes: [{
                name: 'tiny',
                minWidth: 0
            }, {
                name: 'small',
                minWidth: 640
            }, {
                name: 'medium',
                minWidth: 1024
            }, {
                name: 'large',
                minWidth: 1200
            }]
        })

    /** @ngInject */
    function responsiveService($window, $filter, $rootScope, responsiveConfig) {
        var opRegEx = /[<>]=?\s\w+/,
            forEach = angular.forEach,
            filter = angular.filter,
            sizes = responsiveConfig.sizes;

        sizes = $filter('orderBy')(sizes, '-minWidth');

        var getHeight = function() {
                return $window.innerHeight;
            },

            getWidth = function() {
                return $window.innerWidth;
            },

            getLabel = function() {
                var cWidth = getWidth(),
                    returnVal = false;
                for (var i = 0; i < sizes.length; i++) {
                    if (parseInt(cWidth) >= parseInt(sizes[i].minWidth)) {
                        return sizes[i].name;
                    }
                }
            },
            getWidthFromLabel = function(label) {
                return $filter('filter')(sizes, {
                    name: label
                }, true)[0]["minWidth"];
            },

            getTest = function(test) {
                var thingy = test.split(' ')[0],
                    against = test.split(' ')[1];

                if (isNaN(against)) {
                    return eval('(' + getWidth() + ' ' + thingy +  ' ' + getWidthFromLabel(against) + ')');
                } else {
                    return eval('(' + getWidth() + thingy + parseInt(against) + ')');
                }
            },

            getOrientation = function(){
                if(getHeight() > getWidth()) return 'portrait';
                else return 'landscape';
            };

        angular.element($window).on('resize', function() {
            $rootScope.$digest();
        });

        return function(onwha) {
            if (!onwha) {
                return getLabel();
            } else if (onwha == 'width') {
                return getWidth();
            } else if (onwha == 'height') {
                return getHeight();
            } else if (onwha == 'orientation') {
                return getOrientation();
            } else if (opRegEx.test(onwha)) {
                return getTest(onwha);
            } else {
                return (getLabel() == onwha);
            }
            return false;
        };
    }
})();
(function() {
    'use strict';

    angular
        .module('uclguide')
        .factory('preloader', preloader);


    function preloader($q, $rootScope){
        // I manage the preloading of image objects. Accepts an array of image URLs.
        function Preloader( imageLocations ) {
            // I am the image SRC values to preload.
            this.imageLocations = imageLocations;
            // As the images load, we'll need to keep track of the load/error
            // counts when announing the progress on the loading.
            this.imageCount = this.imageLocations.length;
            this.loadCount = 0;
            this.errorCount = 0;
            // I am the possible states that the preloader can be in.
            this.states = {
                PENDING: 1,
                LOADING: 2,
                RESOLVED: 3,
                REJECTED: 4
            };
            // I keep track of the current state of the preloader.
            this.state = this.states.PENDING;
            // When loading the images, a promise will be returned to indicate
            // when the loading has completed (and / or progressed).
            this.deferred = $q.defer();
            this.promise = this.deferred.promise;

        }
        // ---
        // STATIC METHODS.
        // ---
        // I reload the given images [Array] and return a promise. The promise
        // will be resolved with the array of image locations.
        Preloader.preloadImages = function( imageLocations ) {
            var preloader = new Preloader( imageLocations );
            return( preloader.load() );
        };
        // ---
        // INSTANCE METHODS.
        // ---
        Preloader.prototype = {
            // Best practice for "instnceof" operator.
            constructor: Preloader,
            // ---
            // PUBLIC METHODS.
            // ---
            // I determine if the preloader has started loading images yet.
            isInitiated: function isInitiated() {
                return( this.state !== this.states.PENDING );
            },
            // I determine if the preloader has failed to load all of the images.
            isRejected: function isRejected() {
                return( this.state === this.states.REJECTED );
            },
            // I determine if the preloader has successfully loaded all of the images.
            isResolved: function isResolved() {
                return( this.state === this.states.RESOLVED );
            },
            // I initiate the preload of the images. Returns a promise.
            load: function load() {
                // If the images are already loading, return the existing promise.
                if ( this.isInitiated() ) {
                    return( this.promise );
                }
                this.state = this.states.LOADING;
                for ( var i = 0 ; i < this.imageCount ; i++ ) {
                    this.loadImageLocation( this.imageLocations[ i ] );
                }
                // Return the deferred promise for the load event.
                return( this.promise );
            },
            // ---
            // PRIVATE METHODS.
            // ---
            // I handle the load-failure of the given image location.
            handleImageError: function handleImageError( imageLocation ) {

                this.errorCount++;
                // If the preload action has already failed, ignore further action.
                if ( this.isRejected() ) {
                    return;
                }
                this.state = this.states.REJECTED;
                this.deferred.reject( imageLocation );
            },
            // I handle the load-success of the given image location.
            handleImageLoad: function handleImageLoad( imageLocation ) {
                this.loadCount++;
                // If the preload action has already failed, ignore further action.
                if ( this.isRejected() ) {
                    return;
                }
                // Notify the progress of the overall deferred. This is different
                // than Resolving the deferred - you can call notify many times
                // before the ultimate resolution (or rejection) of the deferred.
                this.deferred.notify({
                    percent: Math.ceil( this.loadCount / this.imageCount * 100 ),
                    imageLocation: imageLocation
                });
                // If all of the images have loaded, we can resolve the deferred
                // value that we returned to the calling context.
                if ( this.loadCount === this.imageCount ) {
                    this.state = this.states.RESOLVED;
                    this.deferred.resolve( this.imageLocations );
                }
            },
            // I load the given image location and then wire the load / error
            // events back into the preloader instance.
            // --
            // NOTE: The load/error events trigger a $digest.
            loadImageLocation: function loadImageLocation( imageLocation ) {
                var preloader = this;
                // When it comes to creating the image object, it is critical that
                // we bind the event handlers BEFORE we actually set the image
                // source. Failure to do so will prevent the events from proper
                // triggering in some browsers.
                var image = $( new Image() )
                    .load(
                        function( event ) {
                            // Since the load event is asynchronous, we have to
                            // tell AngularJS that something changed.
                            $rootScope.$apply(
                                function() {
                                    preloader.handleImageLoad( event.target.src );
                                    // Clean up object reference to help with the
                                    // garbage collection in the closure.
                                    preloader = image = event = null;
                                }
                            );
                        }
                    )
                    .error(
                        function( event ) {
                            // Since the load event is asynchronous, we have to
                            // tell AngularJS that something changed.
                            $rootScope.$apply(
                                function() {
                                    preloader.handleImageError( event.target.src );
                                    // Clean up object reference to help with the
                                    // garbage collection in the closure.
                                    preloader = image = event = null;
                                }
                            );
                        }
                    )
                    .prop( "src", imageLocation )
                    ;
            }
        };
        // Return the factory instance.
        return( Preloader );
    }

})();
