(function() {
  'use strict';

  angular
    .module('uclguide')
    .directive('infoContent', infoContent)

  /** @ngInject */
  function infoContent ($log,deviceDetector) {
    return {
      restrict: 'E',
      replace: true,
      transclude:true,
      templateUrl: 'app/info/infocontent.html',
      scope: {
        num:'=',
      },
      controller: function($scope,$rootScope){
        var vm = this;
        vm.page = $scope.num;
        vm.isMobile = deviceDetector.isPhoneOnly();

        vm.inOutEasing = function(t, b, c, d){
          var ts=(t/=d)*t;
          var tc=ts*t;
          return b+c*(tc + -3*ts + 3*t);
        }

        vm.afterSnap = function($index){
          $log.debug('change logo',$index)
          $scope.snapIndex = $index;
          if($index == 0){
            $rootScope.$broadcast('onLogoToggle',{color:'w'});
          }
          else{
            if(deviceDetector.isPhoneOnly()){
              $rootScope.$broadcast('onLogoToggle',{color:'w'});
            }
            else{
              $rootScope.$broadcast('onLogoToggle',{color:'b'});
            }
          }
        }
      },
      controllerAs:'main',
      link: function ($scope, $element, $attri) {

      }
    }
  }
})();
