(function() {
    'use strict';

    angular
        .module('uclguide')
        .controller('InterfaceController', InterfaceController);

    /** @ngInject */
    function InterfaceController(VideoService,deviceDetector) {
        var vm = this;
        vm.videoID = VideoService.current;
        if(deviceDetector.isMobile() && !deviceDetector.isTablet() && !deviceDetector.isDesktop()){
            vm.mobile = true;
            vm.other = false;
        }
        else{
            vm.other = true;
            vm.mobile = false;
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('uclguide')
        .controller('InfoVideoController', InfoVideoController);

    /** @ngInject */
    function InfoVideoController($scope, $stateParams, $timeout, $log,VideoService,$rootScope,deviceDetector) {
        var vm = this;
        vm.videoID = VideoService.current;
        vm.videoInfo = VideoService.getVideoInfoById(vm.videoID);

        $scope.$watch(function(){return VideoService.getCurrent()}, function(n,o){
            if(n!==o){
                vm.videoID = n;
            }
        })

        if(deviceDetector.isMobile() && !deviceDetector.isTablet() && !deviceDetector.isDesktop()){
            $rootScope.$broadcast('onLogoToggle',{color:'w'});
        }
        else{
            $rootScope.$broadcast('onLogoToggle',{color:'b'});
        }


    }
})();
(function() {
  'use strict';

  angular
    .module('uclguide')
    .controller('GuideController', GuideController);

  /** @ngInject */
  function GuideController($scope,$state,$stateParams,$timeout,$log,VideoService,deviceDetector) {
    var vm = this;

    vm.videoID;

    function startInterface(){
      $timeout(function(){

        if(deviceDetector.isMobile() && !deviceDetector.isTablet() && !deviceDetector.isDesktop()){
          vm.mobile = true;
          vm.other = false;
        }
        else{
          vm.other = true;
          vm.mobile = false;
        }

        if(VideoService.setCurrent($stateParams.id) !== null){
            vm.videoID = VideoService.setCurrent($stateParams.id);
        }
        else{
          if(!vm.mobile){
            $state.go('app.video.interface.item',{id:0})
          }
        }
      },100)
    }

    startInterface();

  }
})();

