    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;


    // Set default transitions to use if unspecified
    gsapifyRouterProvider.defaults = {
      // name of transition to use or 'none'
      enter: 'inAnim',
      leave: 'outAnim'
    };

